<!--智能导诊头部-->
<template>
  <div class="header">
     <!--返回首页-->
      <div @click="backHome" class="header-backHome">
        <!--返回图标-->
        <span class="iconfont icon-fanhui back-icon"></span>
        <!--返回文字-->
        <span>返回</span>
      </div>
    <!--标题-->
    <div class="header-title">智能导诊</div>
  </div>
</template>

<script>
  export default {
    name: "IntelligentTreatHeader",
    methods:{
      //返回首页
      backHome(){
        this.$router.push('/home')
      }
    }
  }
</script>

<style lang="css" scoped>

  .header-backHome{
    font-family:'微软雅黑';
    background-color:#ffffff;
    color: black;
    font-weight: 400;
    line-height: 40px;
    font-size:16px;
    vertical-align:middle;

  }
    .back-icon{
      font-weight: bolder;
      font-size:16px;
    }
  .header-title{
    vertical-align:middle;
    font-family:'微软雅黑';
    border:0px;
    font-weight: 400;
    font-size:16px;
    line-height:43px;
    background-color: rgba(119,86,55,1);
    color: #ffffff;
    text-align: center;
  }


</style>



